/*!
 * accordeon - 0.1.0 */
.br-accordeon {
  border-top: 1px solid #9e9d9d;
  margin: 0 0 24px;
}

.br-accordeon .field {
  border-bottom: 1px solid #9e9d9d;
  display: flex;
  flex-direction: column;
}

.br-accordeon .field.is-active .content {
  display: block;
}

.br-accordeon .header {
  background: transparent;
  border: 0;
  color: #1351b4;
  display: flex;
  font-size: 16px;
  justify-content: space-between;
  padding: 8px 16px 12px;
  text-align: left;
  text-decoration: none;
  width: 100%;
}

.br-accordeon .header:hover {
  color: #2b74e9;
  text-decoration: none;
}

.br-accordeon .header:hover .title {
  color: inherit;
}

.br-accordeon .icon {
  padding: 0 16px;
}

.br-accordeon .icon:first-child {
  padding-left: 0;
}

.br-accordeon .icon:last-child {
  padding-right: 0;
}

.br-accordeon .title {
  color: #555555;
  flex: 1;
  font-weight: 400;
  padding: 0 8px;
}

.br-accordeon .title:first-child {
  padding-left: 0;
}

.br-accordeon .title:last-child {
  padding-right: 0;
}

.br-accordeon .content {
  display: none;
  font-size: 14px;
  margin: 0 16px;
  padding: 8px 56px 24px;
}

.br-accordeon .content p:last-child {
  margin-bottom: 0;
}
